.container{
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.card-head{
    max-width: 400px;
    border-radius: var(--primary-border-radius) var(--primary-border-radius) 0 0;
}

.card{
    max-width: 400px;
    box-shadow: 0 0 10px 0 rgba(0,0,0,0.2);
}


/* Inputs */
.input{
    width: 100%;
    padding: 10px;
    border: var(--primary-border-size) solid var(--primary-border-color);
    outline: none;
    border-radius: 5px;
}

.input:focus{
    border-color: var(--secondary-border-color);
}

/* Checkbox */
.checkbox{
    width: 15px;
    height: 15px;
    border: var(--primary-border-size) solid var(--primary-border-color);
    box-shadow: 0 0 5px 0 rgba(0,0,0,0.2);
}

/* Selection */
.user-s-none{
    user-select: none;
}

/* Cursors */
.cursor-p{
    cursor: pointer;
}

/* Buttons */
.btn-primary{
    width: 100%;
    padding: 10px;
    background: var(--primary-button-color);
    color: var(--primary-font-color);
    border: none;
    outline: none;
    border-radius: var(--primary-border-radius);
    cursor: pointer;
}

.btn-primary:hover{
    background: var(--secondary-button-color);
}

.btn-secondary{
    padding: 0.3rem 0.5rem;
    background: var(--secondary-button-color);
    color: var(--primary-font-color);
    font-size: var(--primary-font-size);
    border: none;
    outline: none;
    border-radius: var(--primary-border-radius);
    cursor: pointer;
}

.btn-secondary:hover{
    background: var(--primary-button-color);
}

.m-close-btn{
    position: absolute;
    font-size: var(--primary-font-size);
    top: 0;
    right: 0;
    padding: 5px 10px;
    background: var(--primary-button-color);
    color: var(--primary-font-color);
    border: none;
    outline: none;
    border-radius: 0 0 0 var(--primary-border-radius);
    cursor: pointer;
}


/* Message */
.message{
    animation: message 1s;
    position: relative;
    text-align: center;
    top: 0;
    left: 0;
    margin: 10px 0;
    width: 100%;
    min-height: 40px;
    border-radius: var(--primary-border-radius);
}

/* Animation */
@keyframes message{
    0%{
        opacity: 0;
        transform: translateY(-10px);
    }
    100%{
        opacity: 1;
        transform: translateY(0);
    }
}